.mForeningarCont {
  position: relative;
  display: grid;
  height: 100%;
}
.mForenignarDisplay {
  position: relative;
  background-color: white;
  margin: 15px;
  height: 300px;
  width: 350px;
  overflow-y: scroll;
  display: grid;
  grid-template-rows: auto 1fr;
  color: black;
}
.mForeningarTitles {
  border-bottom: 1px solid rgba(190, 190, 190, 1);
  padding: 10px;
}
.mForeningarList {
  position: relative;
  background-color: rgba(220, 220, 220, 1);
  padding: 10px;
  border-right: 1px solid rgba(190, 190, 190, 1);
  border-left: 1px solid rgba(190, 190, 190, 1);
  border-top: 1px solid rgba(190, 190, 190, 1);
}



.fItemCont {
  position: relative;
  border-bottom: 1px solid rgba(190, 190, 190, 1);
  border-left: 1px solid rgba(190, 190, 190, 1);
  border-right: 1px solid rgba(190, 190, 190, 1);
  display: grid;
  grid-template-columns: 60fr 15fr 15fr;
  background-color: white;
  padding: 5px;
  padding-left: 10px;
}
.fItemTitles {
  display: grid;
  align-items: center;
  grid-template-rows: 50fr 50fr;
}
.fItemAlert {
  position: absolute;
  left: -12px;
  top: -5px;
  width: 22px;
  height: 22px;
  border-radius: 100%;
  background-color: red;
  font-size: 11px;
  color: white;
  display: grid;
  place-items: center;
}
.fItemDelete {
  place-self: center;
  width: 80%;
  height: 80%;
  display: grid;
  place-items: center;
}
.fItemDelete:hover .trashImage{
  background-color: rgba(150, 150, 150, 1);
  border-radius: 5px;
}
.trashImage {
  height: 100%;
  transition: 0.5s;
}
.fItemModalBtn {
  place-self: center;
  width: 90%;
  height: 80%;
  background-color: rgba(230, 230, 230, 1);
  border: none;
  transition: 0.5s;
}
.fItemModalBtn:hover {
  background-color: rgba(240, 240, 240, 1);
}
.fItemExpand p {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  margin-top: 15px;
  margin-bottom: 10px;
  padding-bottom: 5px;
}
.fItemExpand b {
  font-size: 0.9em;
}



.addForeningCont {
  position: absolute;
  display: grid;
  bottom: 10px;
  left: 0;
  width: 100%;
  place-items: center;
}
.addForeningBtn {
  width: 80%;
  min-height: 50px;
  border-radius: 2px;
  background-color: rgba(230, 230, 230, 1);
  color: rgba(50, 50, 50, 1);
  border: none;
  transition: 0.5s;
}
.addForeningBtn:hover {
  background-color: rgba(255, 255, 255, 1);
}

.addModalCont {
  display: grid;
  height: 300px;
  width: 350px;
  margin: 15px;
}
.addFormTitle {
  display: grid;
  place-items: center;
  color: black;
  background-color: white;
  height: 75px;
  border-bottom: 2px solid rgba(200, 200, 200, 1);
}
.addModalForm {
  background-color: rgba(230, 230, 230, 1);
  padding: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: 50px;
  grid-row-gap: 10px;
}
.addModalForm input {
  background-color: white;
  padding: 5px;
}
.addModalCont button {
  background-color: rgba(230, 230, 230, 1);
  border: none;
  color: rgba(50, 50, 50, 1);
  transition: 0.5s;
  border-bottom: 1px solid rgba(100, 100, 100, 1);
}
.addModalCont button:hover {
  background-color: rgba(255, 255, 255, 1);
}
