.mainContainer {
  display: grid;
  grid-template-columns: 100fr 50fr;
  grid-column-gap: 15%;
  margin: 15px;
  background-image: url("../media/background.jpeg");
  background-size: cover;
  min-height: 500px;
  max-height: 1000px;
}
.mainLeft {
  color: white;
  margin: 30px;
  max-width: 550px;
  min-width: 180px;
  white-space: pre-wrap;
}
.mainTitle {
  font-size: 30px;
  margin-bottom: 20px;
}
.mainContent {
  width: 80%;
}
.mainRight {
  background-color: rgba(0, 0, 0, 0.9);
  color: white;
}
