.bannerCont {
  position: relative;
  background-color: red;
  display: grid;
  grid-template-columns: 100fr;
}
.logoCont {
  place-self: center;
}
.bannerImg {
  width: 150px;
  height: 60px;
}


.pLinkCont {
  position: absolute;
  right: 0;
  top: 0;
  display: grid;
  height: 60px;
  width: 25%;
  max-width: 250px;
  min-width: 200px;
  grid-template-columns: 60fr 30fr 10fr;
  padding: 15px;
  place-items: center;
  background-color: darkred;
  color: white;
}
.pLinkNotification {
  background-color: red;
  border-radius: 100%;
  width: 21px;
  height: 21px;
  display: grid;
  place-items: center;
}
