.allCont {
  display: grid;
  grid-template-rows: 60px 100fr auto;
  background-color: rgba(220, 220, 220, 1);
  height: 100%;
  min-height: 100vh;
}
.allSideItemCont {
  padding: 15px;
  padding-top: 0px;
  display: grid;
  grid-template-columns: 33.33fr 33.33fr 33.33fr;
  align-items: start;
  grid-column-gap: 15px;
  transition: 0.5s;
}
@media(max-width: 900px){
  .allSideItemCont {
    grid-template-columns: 100%;
    grid-row-gap: 15px;
    grid-column-gap: 0px;
  }
}
@media (min-width: 1200px) {
  .allSideItemCont {
    grid-template-columns: 33.33fr 33.33fr 33.33fr;
    justify-items: center;
  }
}

.invisible {
  display: none;
}
* {
  box-sizing: border-box;
}
button {
  outline: none;
}
button::-moz-focus-inner {
   border: 0;
}
