.sideItemCont {
  background-color: white;
  padding: 25px;
  padding-bottom: 0px;
}
@media(min-width: 1200px){
  .sideItemCont {
    max-width: 375px;
  }
}

.sideItemTitle {
  font-size: 20px;
}
.sideItemContent {
  white-space: pre-wrap;
}
.sideItemArrows {
  padding-top: 10px;
}
.arrowCont {
  display: grid;
  grid-template-columns: 100fr 15fr;
  padding: 15px;
  padding-right: 30px;
  padding-left: 30px;
  margin-left: -30px;
  margin-right: -30px;
  border-top: 1px solid rgba(220, 220, 220, 1);
}
.arrowContent {
  transition: 0.5s;
}
.arrowArrow {
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  transition: 0.5s;
}
.arrowCont:hover {
  background-color: rgba(0, 0, 0, 0);
  color: rgba(0, 0, 0, 0);
}
